/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */

.widget {
    background-color: $widget-background-color;
    border-color: $widget-border-color;
    border-style: solid;
    border-width: $widget-border-width;
    box-sizing: border-box;
    margin: 0 0 $widget-bottom-margin;
    padding: $widget-inner-padding $widget-bottom-padding;

    .edgtf-widget-title-holder {
        margin-bottom: $widget-list-item-padding;

        .edgtf-widget-title {
            color: $widget-heading-color;
            margin: 0 0 3px;
        }

        .edgtf-separator {
            border-bottom-width: 3px;
            border-color: $widget-text-color;
            width: 30px;
        }

        &:after{
            content: '';
            display: block;
            margin-top: 13px;
            width: 22px;
            background-color: $first-main-color;
            height: 2px;
        }
    }

    ul:not(.select2-selection__rendered) {
        list-style: none;
        margin: 0;
        padding: 6px 0;

        li {
            display: block;
            line-height: $widget-list-item-height;
            margin: 0;
            padding: $widget-list-item-padding 0;

            &:last-child {
                border: 0;
                padding-bottom: 0;
            }

            a {
                color: $widget-link-color;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }
        }

        .children,
        .sub-menu {
            padding-top: 10px;
            padding-left: 10px;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and animation on link hover

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_entries,
    &.widget_nav_menu {
        .edgtf-widget-title {
            margin-bottom: 0;
        }

        ul {
            li {
                overflow: hidden;

                a {
                    display: inline;
                    overflow: hidden;
                    padding: 0;
                    position: relative;
                    @include edgtfTransition(padding .2s ease-in-out);

                    &:after {
                        content: '';
                        position: absolute;
                        left: -15px;
                        width: 2px;
                        color: inherit;
                        background-color: currentColor;
                        height: calc(100% - 9px);
                        top: 50%;
                        @include edgtfTransform(translateY(-50%));
                        font-size: 18px;
                        line-height: inherit;
                        font-weight: normal;
                        font-style: normal;
                        text-rendering: auto;
                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                        @include edgtfTransition(left .2s ease-in-out);
                    }

                    &:hover {
                        padding-left: 15px;

                        &:after {
                            left: 0;
                        }
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and w/o animation on link hover

    &.widget_recent_comments,
    &.widget_rss {
        .edgtf-widget-title {
            margin-bottom: 0;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // calendar

    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;

        }

        th, td {
            padding: 3px 4px;
        }
	
	    td#prev {
		    text-align: left;
	    }
	
	    td#next {
		    text-align: right;
	    }

        td#today {
            background-color: #333;
            color: $widget-text-color;
        }

        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // rss

    &.widget_rss {
        .rss-widget-icon {
            display: inline-block;
        }
	
	    .rsswidget {
		    @include edgtfDefaultHeadingStyle;
	        @include edgtfH6;
	    }

        .rss-date {
            display: block;
            margin-bottom: $widget-list-item-padding;
	        opacity: .5;
        }

        cite {
            display: block;
            margin-top: calc(#{$widget-list-item-padding} / 2);
	        opacity: .5;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // search

    &.widget_product_search, &.widget_search{
        padding: 0;
        border: 0;
    }

    .edgtf-searchform {
        .input-holder {
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;

            input,
            button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: $widget-text-color;
                display: inline-block;
                float: left;
                font-family: $default-text-font;
                font-size: 16px;
                font-weight: 300;
                height: $widget-search-form-height;
                line-height: calc(#{$widget-search-form-height} - 2px);
                outline: 0;
                position: relative;
                vertical-align: top;
                -webkit-appearance: none;

                @include edgtfTransition(color .2s ease-in-out);
            }

            input.search-field {
                padding: 0 0 0 21px;
                width: calc(100% - #{$widget-search-form-height});

                @include edgtfPlaceholder {
                    color: $widget-text-color;
                }
            }

            button {
                background: transparent;
                color: $widget-text-color;
                cursor: pointer;
                font-size: 18px;
                width: $widget-search-form-height;

                @include edgtfTransition(color .2s ease-in-out);

                &:hover {
                    color: $widget-link-hover-color;
                }

                span {
                    display: block;
                    left: 0;
                    line-height: inherit;
                    position: absolute;
                    top: 0;
                    width: 100%;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // combobox

    &.widget_archive,
    &.widget_categories,
    &.widget_text {
        select {
            background-color: $default-background-color;
            border-color: $widget-list-item-border-color;
            border-radius: 0;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 16px;
            font-weight: 500;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            margin-top: $widget-list-item-padding;
            outline: 0;
            padding: 0 0 0 21px;
            position: relative;
            vertical-align: top;
            width: 100%;

            @include edgtfTransition(color .2s ease-in-out);

            option {
                border-color: $widget-border-color;
                border-style: $widget-border-style;
                border-width: $widget-border-width;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // tag cloud

    .tagcloud {
        padding: 16px 0 0;

        a {
            background: transparent;
            border-radius: 2px;
            color: $default-text-color;
            display: inline-block;
            font-size: 16px !important;
            line-height: $widget-tag-cloud-height;
            overflow: hidden;

            &:hover{
                color: $first-main-color;
            }
    
            &:after {
                content:'/';
                padding: 0 20px;
                text-align: center;
                width: 5%;
                overflow: hidden;
                color: $default-text-color !important;
            }
	        
	        @include laptop-landscape-large {
		        font-size: 15px !important;
		
		        &:after {
			        padding: 0 10px 0 13px;
			        width: 4%;
		        }
	        }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // text

    &.widget_text {
        img {
            display: block;
        }

        p {
            &:first-child {
                margin-top: 0;
                padding-top: $widget-list-item-padding;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
	
	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
	// recent comments
	
	.recentcomments > a {
		@include edgtfDefaultHeadingStyle;
	}
}